* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body {
    font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
    background-color: #f1f5f8;
    color: #222;
}
.booklist {
    width: 90vh;
    max-width: 1170px;
    margin: 5rem auto;
    display: grid;
    gap: 2rem;
}
@media (min-width: 768px) {
    .booklist {
        grid-template-columns: repeat(3, 1fr);
        align-items: start;
    }
    
}
.book {
    background-color: #fff;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    position: relative;
}
.book img {
    width: 100%;
    height: 100px;
    object-fit: contain;
}
.book h2 {
    margin-top: 1rem;
    font-size: 1rem;
}
.book h4 {
    font-size: 0.75rem;
    margin-top: 0.5rem;
    letter-spacing: 0.01em;
}
h1 {
    text-align: center;
    margin-top: 4rem;
    text-transform: uppercase;
}